/*fuentes*//*social media*//*home*/#popup {
  visibility: hidden;
  opacity: 0;
  margin-top: -300px;
}

#popup:target {
  visibility: visible;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  z-index: 999;
  transition: all ease 1s;
}

.popup-contenedor {
  position: relative;
  margin: 25% auto;
  padding: 30px 50px;
  background-color: #664596;
  color: #FFA93B;
  border-radius: 3px;
  width: 60%;
}

a.popup-close {
  position: absolute;
  top: 3px;
  right: 3px;
  background-color: #FFA93B;
  padding: 7px 10px;
  font-size: 20px;
  text-decoration: none;
  line-height: 1;
  color: #fff;
  border-radius: 3px;
}

